
body {
  max-height: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Barlow Semi Condensed', sans-serif;
  background: hsl(210, 46%, 95%);
}


// Grid testemunhos 

.testimonials-grid {
  max-width: 1440px;
  padding: 0 30px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, minmax(240px, 260px));
  grid-template-rows: 1fr 1fr;
  gap: 30px;
}

.testimonials-grid > div {
  box-shadow: 0 5px 20px rgba(0,0,0,0.09), 0 3px 20px rgba(0,0,0,.1);
  border-radius: 10px;
}

.testimonials-grid > div:nth-child(1){
  grid-area: 1 / 1 / 2 / 3;
  background: url(../../images/bg-pattern-quotation.svg) no-repeat,  hsl(263, 55%, 52%);
  background-size: 120px 118px;
  background-position: top right 80px;
}

.testimonials-grid > div:nth-child(2){
  background: hsl(217, 19%, 35%);
  grid-area: 1 / 3 / 1 / 4;
}

.testimonials-grid > div:nth-child(3){
  background: hsl(0, 0%, 100%);
  grid-area: 2 / 1 / 2 / 2;
}

.testimonials-grid > div:nth-child(4){
  background: hsl(219, 29%, 14%);
  grid-area: 2 / 2 / 2 / 4;
}

.testimonials-grid > div:nth-child(5){
  background: hsl(0, 0%, 100%);
  grid-area: 1 / 4 / 3 / 5;
}

// testimonials 3 and 5
.testimonials-grid > div:nth-child(2n+3){
  background: hsl(0, 0%, 100%);
  p span:nth-child(1){ // nome
    color: hsl(219, 29%, 14%);
  }
  p:nth-child(2) { // testemunho destaque
    color:  hsl(219, 29%, 14%);
  }
  p:nth-child(3) { // testemunho descrição
    color: hsl(217, 19%, 35%);
  }
}

.testimonials {
  padding: 30px;
  > p:nth-child(2) { // testemunho destaque
    font-size: 1.3em;
    line-height: 1;
    color: hsl(0, 0%, 100%);
    margin-bottom: 20px;
  }
  > p:nth-child(3){ // testemunho descrição
    font-size: 0.9em;
    color: hsl(0, 0%, 81%);
  }
}

.student {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  p {
    line-height: 1;
  }
  p span:nth-child(1){ //name
    color: hsl(0, 0%, 100%);
    font-size: 0.9em;
    font-weight: 700;
  }
  p span:nth-child(3){ // graduate
    font-size: 0.7em;
    color:  hsl(0, 0%, 81%);
  }
 
  img {
    border-radius: 50px;
    height: 32px;
    margin-right: 12px;
    ;
  }
}

// border img student 1 and 4.
.testimonials-grid {
  & > div:nth-child(1){
    img{
      border: 2px solid #a478e7;
     }
  }
  & > div:nth-child(4){
    img{
      border: 2px solid hsl(263, 55%, 52%);
     }
  }
}

