
@media only screen and (max-width: 1115px){
  body {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
  
  .testimonials-grid {
    max-width: 1115px;
    padding: 30px 30px;
    grid-template-columns: repeat(3, minmax(240px, 260px));
    grid-template-rows: 1fr 1fr max-content;
    gap: 30px;
  }

  .testimonials-grid > div:nth-child(1){
    grid-area: 1 / 1 / 2 / 3;
  }

  .testimonials-grid > div:nth-child(2){
    grid-area: 1 / 3 / 1 / 4;
  }

  .testimonials-grid > div:nth-child(3){
    grid-area: 2 / 1 / 2 / 2;
  }

  .testimonials-grid > div:nth-child(4){
    grid-area: 2 / 2 / 2 / 4;
  }

  .testimonials-grid > div:nth-child(5){
    grid-area: 3 / 1 / 3 / -1;
  }


}


@media only screen and (max-width: 855px){
  body {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
  .testimonials-grid {
    max-width: 855px;
    padding: 30px 30px;
    grid-template-columns: repeat(2, minmax(240px, 260px));
    grid-template-rows: 1fr 1fr max-content max-content;
    gap: 30px;
  }

  .testimonials-grid > div:nth-child(1){ // Daniel
    grid-area: 1 / 1 / 2 / 3;
  }

  .testimonials-grid > div:nth-child(2){ // Jonathan
    grid-area: 2 / 2 / 3 / 3;
  }

  .testimonials-grid > div:nth-child(3){ // Jeanette
    grid-area: 2 / 1 / 3 / 2;
  }

  .testimonials-grid > div:nth-child(4){ // Patrick
    grid-area: 3 / 1 / 4 / -1;;
  }

  .testimonials-grid > div:nth-child(5){ // Kira
    grid-area: 4 / 1 / 5 / -1;
  }


}


@media only screen and (max-width: 587px){
  body {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
  .testimonials-grid {
    max-width: 855px;
    padding: 30px 15px;
    grid-template-columns: 160px;
    grid-template-rows: repeat(5, max-content);
    gap: 20px;
  }

  .testimonials-grid > div:nth-child(1){ // Daniel
    grid-column: 1 / 3;
    grid-row: 1 / 2;
  }

  .testimonials-grid > div:nth-child(2){ // Jonathan
    grid-column: 1 / 3;
    grid-row: 2 / 3;
  }

  .testimonials-grid > div:nth-child(3){ // Jeanette
    grid-column: 1 / 3;
    grid-row: 3 / 4;
  }

  .testimonials-grid > div:nth-child(4){ // Patrick
    grid-column: 1 / 3;
    grid-row: 4 / 5;
  }

  .testimonials-grid > div:nth-child(5){ // Kira
    grid-column: 1 / 3;
    grid-row: 5 / 6;
  }

}
